



































































































































































.model-multiple-choice-field {
  .btn {
    padding: 0 7px;
    height: 30px;
    line-height: 33px;
  }
  .uk-table {
    th {
      font-size: 12px;
      padding: 2px 12px;
    }
    td {
      font-size: 14px;
      padding: 4px 12px;
    }
    &.uk-table-striped {
      > tr:nth-of-type(odd), tbody tr:nth-of-type(odd) {
        background: rgba(0,0,0,0.02);
        border-top: 1px solid rgba(0,0,0,0.08);
        border-bottom: 1px solid rgba(0,0,0,0.10);
        &.uk-active {
          background: #ffb;
        }
      }
    }
    &.uk-table-hover {
      > tr:nth-of-type(odd), tbody tr:nth-of-type(odd) {
        &:hover {
          background: #ffb;
        }
      }
    }
  }
}
